<template>
  <div class="homeShow-page">
    <div class="homeShow-content">
      <i class="blue"></i>
      <span class="title">{{$t("home.unit")}}</span>
        <div class="top">
            <div class="top-title">{{biography}}</div>
            <div class="top-second-title">{{$t("home.knowledge")}}</div>
            <div class="top-desc" style="word-wrap: break-word;word-break: normal;">{{customerDescription}}</div>
        </div>
        <div class="picture-Main-box">
            <div class="picture-main-title">
                <span class="picture-indicate">{{$t("home.scene")}}</span>
            </div>
            <div class="picture-list-box">
                <div v-for="(o,index) in classifitionList"
                     :key="index"
                     class="per-list-box">
                    <div class="per-list-title">{{o.pictureClassification}}</div>
                    <div class="per-list-describe-t">{{o.pictureDescription}}</div>
                    <div class="per-images-box">
                        <img v-for="(img,index) in o.imgList"  :key="index" :src="netAPI.findClassificationPictureById+'?id='+img" alt="Not Found">
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import APICONFIG from "../../../../../config/api.js";
export default {
  data() {
    return {
      biography: "KOALA",
      customerDescription: "",
      netAPI: "",
      classifitionList: []
    };
  },
  created() {
    this.netAPI = APICONFIG().get("unit_manage");
    this.getUnitData();
    this.getClassifition()
  },
  methods: {
    getUnitData() {
      this.$http.get(this.netAPI.findCustomerInfoById).then(res => {
        if (res.body.status == 200) {
          this.biography = res.body.data.biography
          this.customerDescription = res.body.data.customerDescription
        }
      }, err => {
        this.$message({
          message: '服务错误' + err,
          type: 'warning'
        });
      });
    },
    getClassifition() {
      this.$http.get(this.netAPI.findCustomerClassificationList).then(res => {
        this.classifitionList = res.body.data;
        console.log(this.classifitionList)
        for (let key of this.classifitionList) {
          this.$set(key, 'imgList', '');
          this.$http.get(this.netAPI.findClassificationPictureIdList, {params: {classificationId: key.id}})
            .then(res => {
              key.imgList = res.body.data;
            }, err => {
              this.$message({
                message: '服务错误' + err,
                type: 'warning'
              });
            });
        }
      })
    }
  }
};
</script>
<style lang="scss" scoped>
.homeShow-page {
  // height: 100%;
  width: 100%;
  .homeShow-content {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 0 60px;
    // overflow-x: hidden;
    background:rgba(255,255,255,1);
    border:1px solid rgba(233,233,233,1);
    border-radius:5px;
    position: relative;
    .blue{
        width:12px;
        height:20px;
        background:rgba(67,131,220,1);
        border-radius:0px 3px 3px 0px;
        position: absolute;
        left: 0;
        top: 19px;
    }
    .title{
      position: absolute;
      left: 20px;
      top: 18px;
      font-size: 20px;
      font-weight: 600;
    }
    .top {
        // height: 600px;
        margin-top: 50px;
        box-sizing: border-box;
        padding: 20px 50px;
        display: flex;
        flex-direction: column;
          .top-title {
            height: 100px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size:26px;
            font-family: Microsoft YaHei;
            font-weight:bold;
          }
          .top-second-title{
            height: 50px;
            display: flex;
            align-items: center;
            font-size:20px;
            font-family: Microsoft YaHei;
            font-weight:bold;
            box-sizing: border-box;
            padding: 0 80px;
            padding-bottom: 15px;
          }
          .top-desc {
              flex: 1;
              box-sizing: border-box;
              padding: 0 80px;
              font-size: 16px;
              line-height: 24px;
              font-family: MicrosoftYaHei;
          }
      }
    .picture-Main-box {
      box-sizing: border-box;
      padding: 20px 50px;
      display: flex;
      flex-direction: column;
        .picture-main-title {
            height: 100px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size:26px;
            font-family: Microsoft YaHei;
            font-weight:bold;
      }
      .picture-list-box {
          flex: 1;
        .per-list-box{
          box-sizing: border-box;
          padding: 0 80px;
          .per-list-title {
            height: 50px;
            display: flex;
            align-items: center;
            font-size:20px;
            font-family: Microsoft YaHei;
            font-weight:bold;
            box-sizing: border-box;
            padding-bottom: 15px;
          }
          .per-list-describe-t {
            flex: 1;
            box-sizing: border-box;
            font-size: 16px;
            line-height: 24px;
            font-family: MicrosoftYaHei;
            padding-bottom: 10px;
          }
          .per-images-box {
            // background: tan;
            display: flex;
            box-sizing: border-box;
            padding: 10px 0;
            flex-wrap: wrap;
            justify-content: space-between;
              >img {
              // width: 357px;
              //width: (99%/3);
              height: 242px;
              margin: 5px 0;
              // flex: 1;
              // height: 242px;
            }
          }
        }
      }
    }
  }
}







</style>



